import { homeList,shopType,detail } from './baseApi'
// axios
import request from '@/utils/request'

// 
export function getHomeList(params={}) {
  return request({
    url: homeList,
    method: 'get',
    params,
    hideloading: false
  })
}

// 
export function getTypeList(params={type:"pop",page:1}) {
  return request({
    url: shopType,
    method: 'get',
    params
  })
}

// 
export function getDetail(iid) {
  return request({
    url: detail,
    method: 'get',
    params:{
        iid
    },
    hideloading: true
  })
}
